.title-container {
    position: relative;
    padding-top: 5vw;
    padding-bottom: 5vw;
    text-align: center;
    width: auto;
    height: auto;
    background-color: rgb(4, 57, 80);
    z-index: 100
}

h2 {
    position: relative;
    font-size: 8vw;
    letter-spacing: .4vw;
    color: white;
}

h2 > span {
    color: rgb(40, 162, 243);
}

h3 {
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: -2vw;
    font-weight: 400;
    font-size: 5vw;
    line-height: 7vw;
    letter-spacing: .4vw;
}

.underline {
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: 5vw;
    width: 25vw;
    height: 1.2vw;
    background-color: white;
    border-radius: 50%;
}

.paragraph {
    position: relative;
    width: auto;
    height: auto;
    margin-top: 15vw;
    margin-left: 6vw;
    margin-right: 6vw;
    font-weight: 300;
    font-size: 4.5vw;
    line-height: 6vw;
    text-align: left;
    letter-spacing: .5vw;
}

.container-icons {
    position: relative;
    margin-top: 25vw;
    text-align: center;
    width: 100%;
    height: auto;
    margin-bottom: 20vw;
}

.faHandHolding {
    font-size: 14vw;
    color: white;
}