header,
footer, 
section, 
article {
    overflow: hidden;
}

section {
    position: relative;
    width: 100%;
    height: auto;
}

ul, li {
    list-style:none;
    margin-top: 0;
    margin-left: 0;
    padding: 0;
}

input, textarea{
    border: 0;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

textarea:focus, input:focus{
    outline: none;
}

textarea {
    border: 0;
    overflow: auto;
    outline: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    resize: none;
}

.form-submit {
	position: absolute;
	height: auto;
    padding: 5vw;
	border: 0;
	border-radius: 1.2vw;
    font-family: Open, sans-serif;
 	font-size: 6vw;
    font-weight: 500;
    letter-spacing: .1vw;
	background-color: rgb(255, 145, 0);
	color: white;
	cursor: pointer;
	transition: background-color .5s;
}
.form-submit:hover {
	transition: background-color .5s;
	background-color: rgb(255, 166, 0);
}

.form-submit:active {
	background-color: rgb(255, 208, 0);
}

.arrow-down {
    width: 10vw;
    height: auto;
    opacity: .6;
} 