.about__icon {
    margin-bottom: -5.6vw;
    font-size: 10.5vw;
    color: rgb(40, 162, 235);
} 

@keyframes bounce { 
    from {transform: translateY(0);} to {transform: translateY(-3vw);} 
}

.about__article {
    position: relative;
    width: 100%;
    height: auto;
    padding-bottom: 45vw;
}

.about__article__container {
    position: relative;
    width: 100%;
    height: auto;
}

.about__article__container__picture {
    position: relative;
    margin-bottom: -2.5vw;
    width: 100vw;
    height: auto;
    filter: brightness(65%);
}

.about__article__container__regard {
    position: absolute;
    top: 0;
    right: 6vw;
    font-weight: 400;
    font-size: 4vw;
    line-height: 6vw;
    text-align: right;
    letter-spacing: .4vw;
    color: white;
}

.about__article__menu {
    position: relative;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0vw;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    flex-direction: row;
    justify-content: center; 
    width: 100%;
    height: 18vw;
    z-index: 200;
}

.about__article__menu--open {
    top: -122vw;
}

.about__article__menu--closed {
    top: 0;
    margin-top: 0;
}

.about__article__menu > a {
    color: white;
    letter-spacing: .3vw;
}

.about__article__menu__title--open {
    text-decoration: none;
    margin-top: 6vw;
    font-size: 7vw;
    font-weight: 500;
    cursor: default;
}

.about__article__menu__title--closed {
    text-decoration: none;
    padding-top: 6vw;
    text-align: center;
    width: 34vw;
    font-size: 5vw;
    font-weight: 500;
    cursor: pointer;
}

.about__article__menu__title--closed-history {
    background-color: rgb(17, 82, 58);
}

.about__article__menu__title--closed-values {
    background-color: rgb(24, 114, 83);
}

.about__article__menu__title--closed-cv {
    background-color: rgb(39, 141, 107);
}

.about__article__collapse {
    position: absolute;
    top: 0;
    width: 100%;
    min-height: 460vw;
    height: auto;
    color: white;
}

.about__article__collapse__history {
    background-color:  rgb(17, 82, 58);
}

.about__article__collapse__values {
    background-color: rgb(24, 114, 83);
}

.about__article__collapse__cv {
    background-color: rgb(39, 141, 107);
    text-align: center;
}

.about__article__collapse--open {
    display: block;
}

.about__article__collapse--closed {
    display: none;
}

.text {
    position: relative;
    width: auto;
    top: 20vw;
    margin-left: 6vw;
    margin-right: 6vw;
    text-align: left;
    letter-spacing: .5vw;
    font-weight: 300;
}

.about__article__collapse__values__text > strong {
    float: right;
}

.about__article__collapse__cv__container  {
    position: relative;
    justify-content: center;
    margin-top: 35vw;
    margin-left: 6vw;
    margin-right: 6vw;
    margin-bottom: 20vw;
    padding: 3vw;
    width: auto;
    height: 261vw;
    color: rgb(17, 82, 58);
    border-radius: 2.5vw;
    background-color: white;
    overflow: hidden;
}

.about__article__collapse__cv__container__name {
    font-family: Open, sans-serif;
    letter-spacing: .4vw;
    font-weight: 300;
    font-size: 7vw;
    text-align: left;
    margin-left: 34vw;
    margin-top: 5vw;
    margin-bottom: 0;
}

.about__article__collapse__cv__container__statut {
    letter-spacing: .3vw;
    font-weight: 400;
    font-size: 5vw;
    text-align: left;
    margin-left: 34vw;
    margin-top: 0;
    margin-bottom: .5vw;
}

.about__article__collapse__cv__container__phone {
    letter-spacing: .3vw;
    font-weight: 600;
    font-family: Open, sans-serif;
    font-size: 5vw;
    text-align: left;
    margin-left: 34vw;
    margin-top: 0;
    margin-bottom: .5vw;
    color: rgb(29, 131, 97);
}

.about__article__collapse__cv__container__mail {
    letter-spacing: .3vw;
    font-weight: 600;
    font-family: Open, sans-serif;
    font-size: 4vw;
    text-align: left;
    margin-left: 34vw;
    margin-top: 0;
    color: rgb(29, 131, 97);
}

.about__article__collapse__cv__container__picture {
    position: relative;
    float: left;
    width: 32vw;
    height: auto;
    border-radius: 2vw;
}

.about__article__collapse__cv__container__certification {
    position: relative;
    margin-top: 5vw;
    font-size: 4vw;
    font-family: Open, sans-serif;
    font-weight: 400;
    line-height: 5vw;
    text-align: start;
}

.about__article__collapse__cv__container__certification > span {
    font-size: 4.6vw;
    font-weight: 500;
    color: rgb(17, 82, 58);
}

.pic-container {
    position: relative;
    top: 25vw;
    display: flex;
    justify-content: center;
    width: auto;
    height: auto;
    margin-left: 6vw;
    margin-right: 6vw;
    margin-top: -3vw;
    margin-bottom: 12vw;
    border-radius: 2.5vw;
    overflow: hidden;
}

.pic {
    position: relative;
    width: 100%;
    height: auto;
    object-fit: cover;
}

.arrow-back {
    position: absolute;
    top: 5vw;
    margin-left: 6vw;
    font-size: 7vw;
    color: white;
    transition: .2s;
    cursor: pointer;
    z-index: 500;
}

.arrow-back:hover {
    font-size: 8vw;
    color: rgb(94, 245, 175);
    transition: .2s;
}

.arrow-back--open {
    display: block;
}

.arrow-back--closed {
    display: none;
}

.about__article__ref {
    position: relative;
    text-align: center;
    margin-bottom: -45vw;
    padding-top: 2vw;
    padding-bottom: 40vw;
    width: 100%;
    height: auto;
    background-color: white;
    color: rgb(22, 99, 73);
}

.about__article__ref__title {
    position: relative;
    margin-top: 10vw;
    margin-bottom: 8vw;
    font-size: 7vw;
    font-weight: 500;
}

.about__article__ref__star {
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: 0;
    top: -5vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 30vw;
    font-size: 5vw;
}

.about__article__ref__text {
    position: relative;
    text-align: center;
    left: 50%;
    transform: translate(-50%, 0);
    width: 88vw;
    line-height: 5vw;
    font-size: 4.8vw;
    font-family: 'Raleway', sans-serif;
    font-style: italic;
    padding-top: 0;
    padding-left: 5vw;
    padding-right: 5vw;
}

.about__article__ref__name {
    position: relative;
    text-align: left;
    font-size: 4.9vw;
    font-style: normal;
    font-weight: 600;
    letter-spacing: .5;
    padding-left: 12vw;
    padding-bottom: 25vw;
}