.form {
	display: flex;
	flex-direction: column;
	position: absolute;
    top: 180vw;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 2;
}

.form__page2 {
    display: none;
}

.form-group > input,
.form-group > textarea {
    border-radius: 1vw;
    width: 80vw;
    margin-bottom: 3vw;
    padding-left: 2vw;
    font-family: Open, sans-serif;
	font-size: 5vw;
    font-weight: 300;
    letter-spacing: .1vw;
    opacity: 1;
    background-color: rgba(0, 62, 88, 0.4);
    transition: .2s;
}

.form-group input:hover,
.form-group textarea:hover {
    background-color: rgba(0, 62, 88, 0.2);
    transition: .2s;
}

input::placeholder,
textarea::placeholder {
    color: white;
    opacity: .7;
}

input:focus::placeholder,
textarea:focus::placeholder {
    color: rgb(90, 90, 90);
}

.form-group input:focus,
.form-group textarea:focus {
    background-color: white;
    color: black;
    transition: .2s;
}

.form-group input {
    height: 14vw;
}

.form-group label {
    display: none;
}

.form__next,
.form__previous {
    text-align: center;
    padding: 5vw;
    margin-bottom: 3vw;
    border-radius: 1vw;
    width: 80vw;
    font-family: Open, sans-serif;
 	font-size: 6vw;
    font-weight: 400;
    letter-spacing: .1vw;
    color: white;
    background-color: rgb(2, 143, 235);
    transition: background-color .2s;
    cursor: pointer;
}

.form__next:hover,
.form__previous:hover {
    background-color: rgb(39, 153, 230);
    transition: .2s;
}

.contact-submit {
    width: 80vw;
}