.navbar {
	position: relative;
    width: auto;
	background: linear-gradient(to top, rgb(81, 87, 92), 20%, transparent);
	transition: background-color 2s;
}

.navbar-closed {
	height: 39.2vw;
	background-color: rgb(26, 31, 34);
	transition: height .3s, background-color .8s;
}

.navbar-open {
	height: 135vw;
	background-color:  rgba(26, 31, 34, .25);;
	transition: height .4s, background-color 1.5s;
}

.navbar__logo {
	position: relative;
	top: 4.5vw;
	margin-left: 4.5vw;
	font-family: 'Open Sans', sans-serif;
	font-size: 14vw;
	font-weight: 700;
	color: rgb(2, 143, 235);
	z-index: 1000;
}

.navbar__logo > span {
	color: rgb(0, 93, 155);
}

.navbar__button__burger {
	position: relative;
    float: right;
    margin-top: 6vw;
	margin-right: 7.73vw;
	width: 10vw;
	height: auto;
	cursor: pointer;
	z-index: 500;
}

menu {
	position: relative;
	display: flex;
	flex-direction: column;
	top: 15vw;
	width: 87vw;
	overflow: hidden;
}

.navbar__menu {
	margin-left: -4.5vw;
}

.navbar__menu-open {
	height: 80vw;
	transition-delay: .2s;
	transition-duration: .25s;
}

.navbar__menu-closed {
	height: 0;
	transition-duration: .1s;
}

.nav__menu__link, a:link, a:visited, a:hover {
	text-decoration: none !important;
}

.nav__menu__link {
	position: relative;
	line-height: 10vw;
	letter-spacing: .35vw;
	border-bottom: 1px solid rgba(107, 177, 224, .4);
    padding-bottom: .5vw;
	cursor: pointer;
	letter-spacing: .3vw;
	font-size: 5.5vw;
	font-weight: 300;
	color: white;
	transition: all .3s;
}

.nav__menu__link:hover {
	border-bottom: 1px solid white;
	text-decoration: none;
	padding-left: 5vw;
	font-size: 5.8vw;
	font-weight: 200;
	color: white;
	transition: all .2s;
}