.banner {
    position: relative;
    width: 100%;
    height: auto;
    background-color: black;
    padding-left: 6.5vw;
}

.banner__title{
    position: relative;
    top: 5vw;
    margin-bottom: 20vw;
    margin-left: -1.5vw;
	font-family: 'Play', sans-serif;
	font-weight: bold;
	font-size: 14.5vw;
	color: rgb(231, 244, 253);
    line-height: 15.5vw;
    letter-spacing: .52vw;
    z-index: 1000;
}

.banner__title > span {
	font-family: 'Open Sans', sans-serif;
	font-size: 28vw;
	font-weight: 700;
}

.banner__title-v {
    color: rgb(2, 143, 235);
}

.banner__title-o {
    color: rgb(0, 93, 155);
}

.banner__title-v,
.banner__title-o {
    animation-duration: 5s;
    animation-name: reveal;
}

@keyframes reveal {
    0%{opacity: 0;}
    12%{opacity: 0;}
    100%{opacity: 1;}
}

.banner__title-w {
    color: rgba(2, 143, 235, .45);
    animation-duration: 8s;
    animation-name: reveal2;
}

@keyframes reveal2 {
    0%{opacity: 0;}
    10%{opacity: 0;}
    100%{opacity: .95;}
}

.banner__title-d {
    color: rgba(2, 143, 235, .14);
    animation-duration: 9s;
    animation-name: reveal3;
}

@keyframes reveal3 {
    0%{opacity: 0;}
    10.5%{opacity: 0;}
    100%{opacity: .95;}
}

.banner__subtitle {
    position: relative;
    top: -10vw;
    margin-bottom: 8vw;
    width: 100%;
    font-size: 8vw;
    font-weight: 400;
    color: rgb(107, 177, 224);
    animation-duration: 3.3s;
    animation-name: train;
    z-index: 1000;
}

@keyframes train {
    0%{margin-left: 100vw;}
    80%{margin-left: 100vw;}
    100%{margin-left: 0;}
}

.banner__text {
    top: -15vw;
    position: relative;
    margin-bottom: 10vw;
    font-size: 3.7vw;
    font-weight: 300;
    line-height: 5vw;
    letter-spacing: .52vw;
    z-index: 1000;
}

.banner__text > span {
    font-family: 'Open Sans', sans-serif;
    font-size: 3.7vw;
}

.banner__text__end {
    font-size: 5vw;
}

.banner__button {
    position: relative;
    left: 23%;
    transform: translate(-50%, 0);
    margin-top: -10vw;
    margin-bottom: 25vw;
    font-size: 14vw;
    color: rgb(41, 235, 2);
    cursor: pointer;
    transition: color 1s;
    z-index: 100;
}

.banner__button:hover {
    color: orange;
    transition: color 1s;
}

.banner__button:active {
    color: white;
}

.banner__picture {
    position: absolute;
    top: 0vw;
    margin-left: -120vw;
    width: 275vw;
    height: auto;
    margin-bottom: -2vw;
    filter: brightness(115%);
    opacity: .3;
}