.footer {
	position: relative;
    width: 100%;
	height: auto;
    text-align: center;
	background-color: black;
}

.footer__fellow-me {
    margin-top: 10vw;
    margin-bottom: 20vw;
}

.footer__link {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    top: -10vw;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: 70vw;
    width: auto;
    text-align: center;
    align-items: center;
}

.footer__link__icon {
	position: relative;
    color: white;
    font-size: 9vw;
    cursor: pointer;
    transition: font-size .2s, color .2s;
}

.footer__link__icon:hover {
	color: rgb(02, 143, 235);
    font-size: 15vw;
    transition: font-size .2s, color .2s;
}

.footer__logo {
	position: relative;
    margin-top: 10vw;;
    margin-bottom: 2vw;
	top: 4.5vw;
    font-family: 'Open Sans', sans-serif;
	font-size: 20vw;
	font-weight: 800;
	letter-spacing: .2vw;
	color: rgb(107, 107, 107);
}

.footer__logo > span {
    color: rgb(85, 85, 85);
}

.footer__website {
    position: relative;
    margin-bottom: 15vw;
    font-family: 'Open Sans', sans-serif;
	font-size: 4vw;
    font-weight: 200;
    color: rgb(192, 192, 192);
    text-decoration: none;
}

.footer__container-info {
    position: relative;
    margin-bottom: -5vw;
    width: 100%;
    height: auto;
    font-family: 'Open', sans-serif;
    background-color: rgba(126, 126, 126, 0.4);
}

.footer__container-info__picture {
    margin-left: -235vw;
    opacity: .4;
}

.footer__container-info__content{
    position: absolute;
    flex-direction: column;
    width: 100%;
    height: auto;
}

.footer__container-info__li {
    position: relative;
    top: -64vw;
    width: 72vw;
    height: 12vw;
    text-align: left;
    margin-left: 20vw;
    font-weight: 100;
    letter-spacing: .1vw;
    color: white;
}

.footer__container-info__li__icon-info {
    font-size: 6.5vw;
}

.footer__container-info__li__text {
    font-size: 3.7vw;
    font-weight: 300;
    margin-top: -6vw;
    margin-left: 11vw;
    left: 11vw;
}

.footer__copyright {
    position: relative;
    width: auto;
    height: 10vw;
    padding-top: .2vw;
    font-size: 3.2vw;
    z-index: 1000;
    background-color: rgb(32, 32, 32);
}